import { StaticSketch } from "../../../components"
import React from "react"
import Seo from "../../../components/seo"
import { Graphics } from "pixi.js"
import { mapToRange } from "../../../utils/mapToRange"

const height = 1024;
const width = 1024;

const Mime4 = () => <StaticSketch
  width={width}
  height={height}
  applicationOptions={{ backgroundColor: [.75, .75, .75] }}
  renderContent={(app, { slope, diagonals, shadingSlope, shadingLayers}) => {
    const maxWeight = height / diagonals;
    const minWeight = height / diagonals / shadingSlope;

    const spacing = height / diagonals;

    const g = new Graphics();

    for (let i = 0; i <= shadingLayers; i++) {
      const weight = mapToRange(i, 0, shadingLayers, minWeight, maxWeight);
      g.lineStyle(weight, 0, 60 / 255);

      for (let i = 0; i < diagonals + 1; i++) {
        g.moveTo(-maxWeight, spacing * i);
        g.lineTo(width / 2, spacing * mapToRange(i, 0, diagonals, -slope, slope) + spacing * i);
        g.lineTo(width + maxWeight, spacing * i);
      }
    }

    app.stage.addChild(g);

  }} sketchOptions={[
  { initialValue: 7, name: 'slope', label: 'Slope' },
  { initialValue: 15, name: 'diagonals', label: 'Diagonals' },
  { initialValue: 30, name: 'shadingSlope', label: 'Shading Slope' },
  { initialValue: 5, name: 'shadingLayers', label: 'Shading Layers' },
]} />

export const Head = () => <Seo title={"Mime 4"} />

export default Mime4;